import React from 'react'

export default function Accueil(props) {
    const voitures = JSON.parse(localStorage.getItem('voituresKey'));
    let test = false;
    if(typeof voitures !== 'undefined' && voitures !==null && voitures.length !== null){
        test = true;
    }
    return (
        <>

            <div className="bg-light bg-gradient p-5">
                <div className="container text-center">
                    <h1 className="display-4">Concessionnaire de voitures</h1>
                    <p className="lead">This is a modified jumbotron that occupies the entire horizontal space of its parent.</p>
                </div>
            </div>
            <div className="row">
                <div className="col-md-8">
                <div className="row">
                {test ? voitures.map((voiture,index) =>{
                    return(
                        <div className="col-md-6" key={index}>
                            <div className="card mb-3">
                                <img className="card-img-top" src={process.env.PUBLIC_URL + '/images/'+voiture.image} alt="Card" />
                                <div className="card-body">
                                    <h5 className="card-title">Voiture N° 00{voiture.id}</h5>
                                    <ul className="list-group list-group-flush">
                                        <li className="list-group-item">Marque: {voiture.marque}</li>
                                        <li className="list-group-item">Modele: {voiture.modele}</li>
                                        <li className="list-group-item">Pays: {voiture.pays}</li>
                                        <li className="list-group-item">Prix: {voiture.prix}€</li>
                                        <li className="list-group-item">Disponible: {voiture.disponible ? 'oui':'non'}</li>
                                    </ul>
                                </div>
                                
                            </div>
                        </div>
                    );
                })
                : 'Pas de voitures disponibles.'
            }
            </div>
           
<button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#exampleModal">
  Launch demo modal
</button>

<div className="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog">
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title" id="exampleModalLabel">Modal title</h5>
        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div className="modal-body">
        ...
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" className="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>
            </div>
                <div className="col-md-4" style={{backgroundColor:""}}> 
                    {/* <Meteo /> */}
                </div>
            </div>
            
            
        </>
    )
}
