import React, { Component } from 'react'
import AutoService from '../Services/AutoService';

export default class Login extends Component {
    constructor(props) {
        super(props)
        this.state = {
                 login:'',
                 pass:'',
                 secret:null,
                 error:'',
                 errorLogin:'',
                 errorPass:''

        }
        //this.state = this.initState;

    }

    componentDidMount = () => {
        AutoService.getLoginPassword().then(secret=>{
            this.setState({secret});
        });
    }

    handleChange = (e)=>{
        const {name, type, checked, value} = e.target;
        let val = type === 'checkbox' ? checked : value;
        let err1='',err2='';
        if (name === "login") {
            if (val === "") {
              err1 = <strong>Le champ login est requis</strong>;
            }
        }else if(name ==="pass"){
            if (val === "") {
                err2 = <strong>Le champ mot de passe est requis</strong>;
              }
        }
        this.setState({errorLogin:err1, errorPass:err2});
        this.setState({[name]:val});

    }

    handleSubmit = (e) => {
        e.preventDefault();
       const {login, pass, secret, errorLogin, errorPass}=this.state;
        if(errorLogin ==="" && errorPass === ""){
            if(login === secret.login && pass === secret.pass){
                const d = new Date();
                const n = d.toLocaleDateString();
                sessionStorage.setItem('token', n);
                this.props.history.push('/admin123');
                window.location.reload();
                // <Redirect to='/admin123' />
            }else{
                const errMessage = 'Le login ou/et mot de passe ne correspondent pas';
                this.setState({error: errMessage});
            }
        }else{
         const errMessage = 'Le login ou/et mot de passe est/sont requis';
         this.setState({error: errMessage});
        }
    }

    render() {
    //AutoService.getLoginPassword().then(x=>console.log(x));
    console.log(this.state.secret);
        return (
            <>
                <div className="card position-absolute top-50 start-50 translate-middle w-25">
                    {this.state.error && <div className="alert alert-danger">{this.state.error}</div>}
                    <div className="card-body">
                    <form onSubmit={this.handleSubmit}>
                    <div className="mb-3">
                        <label htmlFor="login" className="form-label">Identifiant*</label>
                        <input type="text" onChange={this.handleChange} className="form-control" id="login" name="login" placeholder="Votre login"/>
                    </div>
                    {this.state.errorLogin}
                    <div className="mb-3">
                        <label htmlFor="pass" className="form-label">Mot de passe*</label>
                        <input type="password" onChange={this.handleChange} className="form-control" name="pass" id="pass" placeholder="Votre mot de passe"/>
                    </div>
                    {this.state.errorPass}
                    <button type="submit" className="btn btn-primary col-12">Soumettre</button>
                    </form>
                    </div>
                </div>
            </>
        )
    }
}
